  --success-color-4: #0d7a55;
  --success-color-5: #059465;
  --success-color-6: #43af8a;
  --success-color-7: #46bf96;
  --success-color-8: #80d2b6;
  --success-color-9: #b4e1d3;
  --success-color-10: #deede8;
  --gray-color-1: #f3f3f3;
  --gray-color-2: #eee;
  --gray-color-3: #e8e8e8;
  --gray-color-4: #ddd;
  --gray-color-5: #c6c6c6;
  --gray-color-6: #a6a6a6;
  --gray-color-7: #8b8b8b;
  --gray-color-8: #777;
  --gray-color-9: #5e5e5e;
  --gray-color-10: #4b4b4b;
  --gray-color-11: #393939;
  --gray-color-12: #2c2c2c;
  --gray-color-13: #242424;
  --gray-color-14: #181818;
  --font-white-1: rgba(255, 255, 255, 0.9);
  --font-white-2: rgba(255, 255, 255, 0.55);
  --font-white-3: rgba(255, 255, 255, 0.35);
  --font-white-4: rgba(255, 255, 255, 0.22);
  --font-gray-1: rgba(0, 0, 0, 0.9);
  --font-gray-2: rgba(0, 0, 0, 0.6);
  --font-gray-3: rgba(0, 0, 0, 0.4);
  --font-gray-4: rgba(0, 0, 0, 0.26);
  --brand-color: var(--brand-color-8);
  --warning-color: var(--warning-color-5);
  --error-color: var(--error-color-6);
  --success-color: var(--success-color-5);
  --brand-color-hover: var(--brand-color-7);
  --brand-color-focus: var(--brand-color-2);
  --brand-color-active: var(--brand-color-9);
  --brand-color-disabled: var(--brand-color-3);
  --brand-color-light: var(--brand-color-1);
  --brand-color-light-hover: var(--brand-color-2);
  --warning-color-hover: var(--warning-color-4);
  --warning-color-focus: var(--warning-color-2);
  --warning-color-active: var(--warning-color-6);
  --warning-color-disabled: var(--warning-color-3);
  --warning-color-light: var(--warning-color-1);
  --warning-color-light-hover: var(--warning-color-2);
  --error-color-hover: var(--error-color-5);
  --error-color-focus: var(--error-color-2);
  --error-color-active: var(--error-color-7);
  --error-color-disabled: var(--error-color-3);
  --error-color-light: var(--error-color-1);
  --error-color-light-hover: var(--error-color-2);
  --success-color-hover: var(--success-color-4);
  --success-color-focus: var(--success-color-2);
  --success-color-active: var(--success-color-6);
  --success-color-disabled: var(--success-color-3);
  --success-color-light: var(--success-color-1);
  --success-color-light-hover: var(--success-color-2);
  --mask-active: rgba(0, 0, 0, 0.4);
  --mask-disabled: rgba(0, 0, 0, 0.6);
  --bg-color-page: var(--gray-color-14);
  --bg-color-container: var(--gray-color-13);
  --bg-color-container-hover: var(--gray-color-12);
  --bg-color-container-active: var(--gray-color-10);
  --bg-color-container-select: var(--gray-color-9);
  --bg-color-component: var(--gray-color-11);
  --bg-color-component-hover: var(--gray-color-10);
  --bg-color-component-active: var(--gray-color-9);
  --bg-color-component-disabled: var(--gray-color-12);
  --bg-color-special-component: transparent;
  --text-color-primary: var(--font-white-1);
  --text-color-secondary: var(--font-white-2);
  --text-color-placeholder: var(--font-white-3);
  --text-color-disabled: var(--font-white-4);
  --text-color-anti: #fff;
  --text-color-brand: var(--brand-color-8);
  --text-color-link: var(--brand-color-8);
  --border-level-1-color: var(--gray-color-11);
  --component-stroke: var(--gray-color-11);
  --border-level-2-color: var(--gray-color-9);
  --component-border: var(--gray-color-9);
  --shadow-1: 0 4px 6px rgba(0, 0, 0, 0.06), 0 1px 10px rgba(0, 0, 0, 8%), 0 2px 4px rgba(0, 0, 0, 12%);
  --shadow-2: 0 8px 10px rgba(0, 0, 0, 0.12), 0 3px 14px rgba(0, 0, 0, 10%), 0 5px 5px rgba(0, 0, 0, 16%);
  --shadow-3: 0 16px 24px rgba(0, 0, 0, 0.14), 0 6px 30px rgba(0, 0, 0, 12%), 0 8px 10px rgba(0, 0, 0, 20%);
  --shadow-inset-top: inset 0 0.5px 0 #5e5e5e;
  --shadow-inset-right: inset 0.5px 0 0 #5e5e5e;
  --shadow-inset-bottom: inset 0 -0.5px 0 #5e5e5e;
  --shadow-inset-left: inset -0.5px 0 0 #5e5e5e;
  --shadow-out-top: 0 -1px 0 #4b4b4b;
  --shadow-out-right: 1px 0 0 #4b4b4b;
  --shadow-out-bottom: 0 1px 0 #4b4b4b;
  --shadow-out-left: -1px 0 0 #4b4b4b;
  --table-shadow-color: rgba(0, 0, 0, 0.55);
  --scrollbar-color: rgba(255, 255, 255, 0.1);
}
